:root {
  --heading-font: fantasy;
}

.App {
  background-color: #BACFE2;
  min-height: 100vh;
  height: 100vh;
  min-width: 100vw;
  position: relative;
}

p {
  font-size: 1.25em;
}


#innerAttack {
  position: relative;
  transform: translateX(-50%);
  left: 50%;
  top: 15%;
}

button{
  font-size: 15px;
  padding: 7px 10px;
  border-radius: 8px;
  background-color: #73D6F7;
}

h1, h2, h3 {
  font-family: var(--heading-font);
}

#attack {
  background-color: #607699;
  text-align: center;
  display: inline-block;
  padding: 5%;
  position: relative;
  border-style: solid;
  border-radius: 5px;
  border-width: 5px;
  border-color: #2D7BEF;
  
  width: 50%;
  height: 50%;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
}

.inline{
  display: inline;
}